import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";

import ItemTransferCreateForm from "./createForm";

import notification from "../../../../components/notification";
// import { Input, Select, Divider } from "antd";

import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import actions from "../../../../redux/inventory/itemTransfer/actions";
import { Box } from "../../../App/app.style";
import { Tabs } from "antd";

const redirecTo = "/dashboard/inventories/item-transfers";
const TabPane = Tabs.TabPane;

class CreateItemTransfer extends Component {
  componentDidMount() {
    const { createItemTransfer } = this.props;
    createItemTransfer();
  }

  componentDidUpdate() {
    // Display error notif
    if (this.props.itemTransfer.error) {
      notification("error", this.props.itemTransfer.error);
      this.props.clearError();
    }

    if (
      this.props.itemTransfer.success &&
      this.props.itemTransfer.id_item_transfer
    ) {
      this.props.history.replace(
        redirecTo + "/update/" + this.props.itemTransfer.id_item_transfer
      );
      // notification("success", this.props.itemTransfer.success);
      // this.props.clearSuccess();
    }
  }

  handleSubmit = value => {
    const { storeItemTransfer } = this.props;

    // Uppercasing itemTransfer code
    // value.customer_type_code = value.customer_type_code.toUpperCase();
    // console.log(value);
    storeItemTransfer(value);
  };

  render() {
    // console.log(this.props);
    return (
      <LayoutWrapper>
        <Box>
          <PageHeader title={<IntlMessages id="itemTransfers.button.add" />}>
            <Button onClick={() => this.props.history.replace(redirecTo)}>
              <IntlMessages id="button.back" />
            </Button>
          </PageHeader>

          <Tabs defaultActiveKey="1">
            <TabPane tab={<IntlMessages id="jobCostings.tab.detail" />} key="1">
              <ItemTransferCreateForm
                submit={this.handleSubmit}
                itemTransfer={this.props.itemTransfer}
              />
            </TabPane>
            <TabPane
              tab={<IntlMessages id="jobCostings.tab.attachment" />}
              key="2"
              disabled
            />
          </Tabs>
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return {
    itemTransfer: state.itemTransfer,
    auth: state.auth
  };
};

export default compose(
  connect(
    mapStateToProps,
    actions
  )
)(CreateItemTransfer);
