import React, { Component } from "react";
import moment from "moment";
import { connect } from "react-redux";
import { compose } from "redux";
import actions from "../../../../redux/inventory/itemTransfer/actions";

import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import {
  Form,
  Input,
  DatePicker,
  Select,
  Divider,
  // Spin,
  Row,
  Col,
  // Button as ButtonAntd,
  // Empty,
  Tooltip,
  Icon,
  Modal
} from "antd";

import EditableTable from "./editableTable";
import ModalTransaction from "./modal";
// import { Object } from "core-js";

const { Option } = Select;
const { TextArea } = Input;
const InputGroup = Input.Group;

const confirm = Modal.confirm;

const { getItems, getTransactions } = actions;

class ItemTransferCreateForm extends Component {
  state = {
    id_from_location: null,
    id_to_location: null,
    item: null,
    items: [],
    itemDetails: {
      item_transfer_details: []
    },
    importedTransaction: 0,
    search: null,
    viewed: false,
    visible: false,
    clear: false,
    err: false,
    err_item: false
  };

  componentDidUpdate() {
    const { viewed } = this.state;
    const { prevTransConfirm } = this.props.itemTransfer;

    /* Show modal transaction yang harus di proses */
    if (prevTransConfirm && !viewed) {
      this.showTransaction();
      this.setState({ viewed: true });
    }
  }

  // Show transaction yang harus diproses
  showTransaction = () => {
    const { prevTransConfirm } = this.props.itemTransfer;

    confirm({
      title: prevTransConfirm.title,
      content: prevTransConfirm.content,
      icon: <Icon type="info-circle" />,
      okText: prevTransConfirm.ok_button,
      cancelText: prevTransConfirm.cancel_button,
      onOk: () => {
        this.handleModal();
      },
      onCancel() {}
    });
  };

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFieldsAndScroll((err, values) => {
      const { itemDetails } = this.state;

      // validasi item
      if (itemDetails.item_transfer_details.length <= 0) {
        return this.setState({ err_item: true });
      } else {
        this.setState({ err_item: false });
      }

      if (err) {
        this.setState({ err: true });
        return;
      }

      values.transaction_date = values.transaction_date.format(
        "YYYY-MM-DD HH:mm:ss"
      );
      const data = Object.assign({}, values, itemDetails);
      // console.log(data);
      if (!err) {
        this.setState({ err: false });
        this.props.submit(data);
      }
    });
  };

  handleSelectLocation = (name, id_location) => {
    const { getItems } = this.props;
    this.setState({
      [name]: id_location,
      clear:
        name === "id_to_location" && this.state.id_to_location !== id_location
          ? true
          : false
    });

    if (name === "id_from_location") {
      getItems(id_location);
    }
  };

  handleModal = () => {
    const { visible } = this.state;
    visible
      ? this.setState({ visible: false })
      : this.setState({ visible: true });
  };

  handleSelectItem = value => {
    const { items } = this.props.itemTransfer;
    const { search } = this.state;
    const { getFieldValue } = this.props.form;
    const id_from_location = getFieldValue("id_from_location");
    const { getItems } = this.props;
    // console.log(items.filter(item => item.id_item === value));
    this.setState({
      item: items.find(item => item.id_item === value)
    });

    if (search) {
      getItems(id_from_location);
      this.setState({ search: null });
    }
  };

  handleSelectModal = value => {
    const { transactions } = this.props.itemTransfer;
    const { itemDetails } = this.state;
    const { setFieldsValue, getFieldValue } = this.props.form;

    let transaction = transactions.find(
      curr_data => curr_data.id_item_request === value
    );

    const find = itemDetails.item_transfer_details.findIndex(
      currData => currData.id_item_request === transaction.id_item_request
    );

    if (find < 0) {
      transaction.item_request_details.map(
        data => (data.item_request_no = transaction.transaction_no)
      );
      this.setState({
        items: [
          ...itemDetails.item_transfer_details,
          ...transaction.item_request_details
        ],
        id_to_location: transaction.id_location,
        clear: false
      });
    }

    if (transaction.id_location === getFieldValue("id_from_location")) {
      setFieldsValue({ 
        id_to_location: transaction.id_location,
        id_from_location: undefined
      });
    } else {
      setFieldsValue({ id_to_location: transaction.id_location });
    }
    this.handleModal();
  };

  handleSearchItem = value => {
    const { getItems } = this.props;
    const { getFieldValue } = this.props.form;
    const id_from_location = getFieldValue("id_from_location");
    // console.log(this.props.purchaseInvoice);
    this.setState({ search: value });
    if (id_from_location) {
      getItems(id_from_location, value);
    }
  };

  handleChangeItem = value => {
    this.setState({ search: value });
  };

  handleChangeTransDate = status => {
    if (!status) {
      const { getFieldsValue } = this.props.form;
      const { getTransactions } = this.props;

      let { transaction_date, id_to_location } = getFieldsValue([
        "transaction_date","id_to_location"
      ]);
      transaction_date = 
        moment(transaction_date.format("YYYY-MM-DD HH:mm:ss")).format("YYYY-MM-DD HH:mm:ss");
      const filter = `id_location=${id_to_location}&transaction_date=${transaction_date}`;
      getTransactions(1, null, filter);
    }
  }

  handleSaveItemList = (value, importedTransaction = 0) => {
    this.setState({
      itemDetails: {
        item_transfer_details: value
      },
      importedTransaction,
      clear: false
    });
  };

  clearItem = value => {
    this.setState({
      item: null,
      itemDetails: {
        item_transfer_details: value
      },
      clear: false
    });
  };

  clearItems = (value, importedTransaction = 0) => {
    this.setState({
      items: [],
      itemDetails: {
        item_transfer_details: value
      },
      importedTransaction,
      clear: false
    });
  };

  render() {
    // console.log(this.props.itemTransfer.transactions);
    const { visible, importedTransaction } = this.state;
    const { getFieldDecorator, getFieldValue } = this.props.form;
    const { itemTransfer } = this.props;
    const {
      // items,
      fromLocations,
      toLocations,
      couriers,
      adjustmentTypes
    } = itemTransfer ? itemTransfer : [];
    const { user } = this.props;
    let { permissions, default_employee_location } = user ? user : [];
    permissions = permissions ? permissions : [];
    default_employee_location = default_employee_location
      ? default_employee_location
      : [];
    const {
      loading,
      loadingSubmit,
      loadingItems,
      loadingForeign,
      loadingTransactions
    } = itemTransfer;
    // const data = itemTransfer.itemTransfer ? itemTransfer.itemTransfer : [];

    // const used = data.is_used;
    const error = itemTransfer.errorField ? itemTransfer.errorField : [];
    // console.log(data);

    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };
    const formTextArea = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };
    const tailFormItemLayout = {
      wrapperCol: {
        xs: {
          span: 24,
          offset: 0
        },
        sm: {
          span: 24,
          offset: 0
        }
      }
    };

    return (
      <Form layout="vertical" onSubmit={this.handleSubmit}>
        <ModalTransaction
          visible={visible}
          itemTransfer={itemTransfer}
          id_location={getFieldValue("id_to_location")}
          transaction_date={getFieldValue("transaction_date")
            ? moment(getFieldValue("transaction_date")).format("YYYY-MM-DD HH:mm:ss")
            : moment().format("YYYY-MM-DD HH:mm:ss")
          }
          handleModal={this.handleModal}
          handleSelectModal={this.handleSelectModal}
        />

        <Row>
          <Col span={12} style={{ paddingRight: "15px" }}>
            <InputGroup>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="itemTransfers.transactionNo" />}
                >
                  {getFieldDecorator("item_transfer_no", {
                    // initialValue: data.item_transfer_no,
                  })(<Input disabled={true} />)}
                </Form.Item>
              </Col>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={
                    <Tooltip
                      placement="top"
                      title={
                        <IntlMessages id="transactionDateHelp" />
                      }
                    >
                      <IntlMessages id="itemTransfers.transactionDate" />
                      <Icon
                        style={{ marginLeft: "5px" }}
                        type="question-circle"
                        theme="filled"
                      />
                    </Tooltip>
                  }
                  style={{ marginBottom: 0, paddingBottom: 0 }}
                >
                  {getFieldDecorator("transaction_date", {
                    initialValue: moment()
                  })(
                    <DatePicker
                      style={{ width: "100%" }}
                      showTime={{ format: "HH:mm:ss" }}
                      format="DD MMMM YYYY HH:mm:ss"
                      disabled={
                        !permissions.includes("Can Back Date") || importedTransaction > 0
                        ? true 
                        : false
                      }
                      allowClear={false}
                      disabledDate={e => e.valueOf() > moment().endOf("day")}
                      onOpenChange={this.handleChangeTransDate}
                    />
                  )}
                </Form.Item>
              </Col>
            </InputGroup>

            <InputGroup>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="itemTransfers.fromLocation" />}
                >
                  {getFieldDecorator("id_from_location", {
                    initialValue: default_employee_location.id_location,
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      }
                    ]
                  })(
                    <Select
                      showSearch
                      style={{ width: "100%" }}
                      placeholder={
                        <IntlMessages id="itemTransfers.fromLocation" />
                      }
                      loading={loadingForeign}
                      onSelect={this.handleSelectLocation.bind(
                        this,
                        "id_from_location"
                      )}
                      optionFilterProp="children"
                      filterOption={true}
                      allowClear
                    >
                      {fromLocations.map((data, key) =>
                        data.id_location !== getFieldValue("id_to_location") ? (
                          <Option key={key} value={data.id_location}>
                            {`${data.location_code} (${data.location_name})`}
                          </Option>
                        ) : null
                      )}
                    </Select>
                  )}
                </Form.Item>
              </Col>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="itemTransfers.toLocation" />}
                >
                  {getFieldDecorator("id_to_location", {
                    // initialValue: data.id_to_location,
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      }
                    ]
                  })(
                    <Select
                      showSearch
                      style={{ width: "100%" }}
                      placeholder={
                        <IntlMessages id="itemTransfers.toLocation" />
                      }
                      loading={loadingForeign}
                      onSelect={this.handleSelectLocation.bind(
                        this,
                        "id_to_location"
                      )}
                      optionFilterProp="children"
                      filterOption={true}
                      allowClear
                    >
                      {toLocations.map((data, key) =>
                        data.id_location !==
                        getFieldValue("id_from_location") ? (
                          <Option key={key} value={data.id_location}>
                            {`${data.location_code} (${data.location_name})`}
                          </Option>
                        ) : null
                      )}
                    </Select>
                  )}
                </Form.Item>
              </Col>
            </InputGroup>
          </Col>
          <Col span={12} style={{ paddingLeft: "15px" }}>
            <Form.Item
              {...formTextArea}
              label={<IntlMessages id="itemTransfers.note" />}
            >
              {getFieldDecorator("note", {
                // initialValue: data.note
              })(<TextArea autosize={{ minRows: 4 }} />)}
            </Form.Item>
          </Col>
        </Row>

        <Row>
          <Col span={12} style={{ paddingRight: "15px" }}>
            <InputGroup>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="itemTransfers.courier" />}
                >
                  {getFieldDecorator("id_courier", {
                    // initialValue: data.id_courier,
                  })(
                    <Select
                      showSearch
                      allowClear
                      style={{ width: "100%" }}
                      placeholder={<IntlMessages id="itemTransfers.courier" />}
                      loading={loadingForeign}
                      optionFilterProp="children"
                      filterOption={true}
                    >
                      {couriers.map((data, key) => (
                        <Option key={key} value={data.id_courier}>
                          {`${data.courier_name} (${data.description})`}
                        </Option>
                      ))}
                    </Select>
                  )}
                </Form.Item>
              </Col>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="itemTransfers.transferType" />}
                >
                  {getFieldDecorator("id_adjustment_type", {
                    // initialValue: data.id_adjustment_type,
                  })(
                    <Select
                      showSearch
                      allowClear
                      style={{ width: "100%" }}
                      placeholder={
                        <IntlMessages id="itemTransfers.transferType" />
                      }
                      loading={loadingForeign}
                      optionFilterProp="children"
                      filterOption={true}
                    >
                      {adjustmentTypes.map((data, key) => (
                        <Option key={key} value={data.id_adjustment_type}>
                          {`${data.adjustment_type_code} (${data.description})`}
                        </Option>
                      ))}
                    </Select>
                  )}
                </Form.Item>
              </Col>
            </InputGroup>
          </Col>
          <Col span={12} style={{ paddingLeft: "15px" }}>
            <InputGroup>
              <Col span={8}>
                <Form.Item
                  {...formItemLayout}
                  label={
                    <Tooltip
                      placement="top"
                      title={<IntlMessages id="purchaseOrders.importRQ.help" />}
                    >
                      <IntlMessages id="purchaseOrders.importRQ" />
                      <Icon
                        style={{ marginLeft: "5px" }}
                        type="question-circle"
                        theme="filled"
                      />
                    </Tooltip>
                  }
                >
                  <Button
                    icon="select"
                    loading={loadingTransactions}
                    disabled={
                      loadingTransactions || !getFieldValue("id_to_location")
                    }
                    onClick={this.handleModal}
                    style={{ height: "31px", width: "100%" }}
                    // shape="circle"
                  >
                    <IntlMessages id="button.import" />
                  </Button>
                </Form.Item>
              </Col>
              <Col span={16} />
            </InputGroup>
          </Col>
        </Row>

        <Divider orientation="left" style={{ fontSize: 14 }}>
          <IntlMessages id="itemTransfers.subtitle.itemList" />
        </Divider>

        {/* <Row>
          <Col span={24}>
            <Form.Item
              {...formItemLayout}
              extra={<IntlMessages id="itemTransfers.item.help" />}
              validateStatus={error.item_transfer_details ? "error" : null}
              help={
                error.item_transfer_details ? error.item_transfer_details : null
              }
            >
              <Select
                value={search ? search : []}
                showSearch
                allowClear
                // optionFilterProp="children"
                filterOption={false}
                placeholder={
                  <IntlMessages id="itemTransfers.item.placeholder" />
                }
                loading={loadingItems}
                notFoundContent={
                  loadingItems ? (
                    <Spin size="small" />
                  ) : getFieldValue("id_from_location") ? (
                    <Empty description={<IntlMessages id="noData" />} />
                  ) : (
                    <Empty
                      description={
                        <IntlMessages id="itemTransfers.item.noLocation" />
                      }
                    />
                  )
                }
                onSelect={this.handleSelectItem}
                onSearch={this.handleSearchItem}
              >
                {items.map(data => (
                  <Option key={data.id_item} value={data.id_item}>
                    {data.item_code + " - " + data.item_name}
                  </Option>
                ))}
              </Select>
            </Form.Item>
          </Col>
        </Row> */}

        <Row>
          <Col span={24}>
            <EditableTable
              item={this.state.item}
              items={this.state.items}
              status={1}
              handleSaveItemList={this.handleSaveItemList}
              clearItem={this.clearItem}
              clearItems={this.clearItems}
              loadingTransactions={loadingTransactions}
              loadingItems={loadingItems}
              clear={this.state.clear}
            />
          </Col>
        </Row>

        <Form.Item {...tailFormItemLayout}>
          <Button
            type="primary"
            htmlType="submit"
            style={{ marginTop: "50px" }}
            loading={loading || loadingSubmit}
            disabled={!permissions.includes("Create Item Transfer")}
          >
            <IntlMessages id="button.submit" />
          </Button>
          
          <br />

          {this.state.err ? (
            <span style={{ color: "#f5222d" }}>
              <IntlMessages id="form.error" />
            </span>
          ) : null}

          {this.state.err_item ? (
            <IntlMessages id="itemDataEmpty">
              {txt => <span style={{ color: "#f5222d" }}>{txt}</span>}
            </IntlMessages>
          ) : null}
        </Form.Item>
      </Form>
    );
  }
}

const WrappedItemTransferCreateForm = Form.create({
  name: "create_item_transfer"
})(ItemTransferCreateForm);

const mapStateToProps = state => {
  return {
    user: state.auth.user,
    auth: state.auth
  };
};

export default compose(
  connect(
    mapStateToProps,
    { getItems, getTransactions }
  )
)(WrappedItemTransferCreateForm);
