import React, { Component } from "react";

import { Button, Popconfirm, Tooltip, Form } from "antd";
import EditableCell from "./editableCell";
import EditableContext from "./editableContext";
import Scrollbars from "../../../../components/utility/customScrollBar";
import TableWrapper from "../../../Tables/antTables/antTable.style";
import "../../../App/editableStyle.css";
import IntlMessages from "../../../../components/utility/intlMessages";
import { getUnitConversion } from "../../../../helpers/helper";
import NumberFormat from "../../../App/NumberFormat";
import getLanguage from "../../../App/MyLanguange";
import CardWrapper from "../../../App/app.style";

const EditableRow = ({ form, index, ...props }) => (
  <EditableContext.Provider value={form}>
    <tr {...props} />
  </EditableContext.Provider>
);

const EditableFormRow = Form.create()(EditableRow);

const getTotal = arr => {
  let totalQty = 0;
  arr.map(data => {
    totalQty += parseFloat(data.qty);
  });

  return totalQty;
};

class EditableTable extends Component {
  constructor(props) {
    super(props);
    const { items } = props;

    this.state = {
      dataSource: [...items],
      count: items.length + 1,
      totalQty: 0,
      importedTransaction: 0,
    };
  }

  componentDidUpdate() {
    const {
      loadingTransactions,
      loadingItems,
      // item,
      items,
      // clearItem,
      clearItems,
      handleSaveItemList,
      clear
    } = this.props;

    // if (!loadingTransactions && !loadingItems && item) {
    //   const { dataSource } = this.state;
    //   const { count, totalQty } = this.state;
    //   // console.log(item.length);
    //   const find = dataSource.findIndex(
    //     currData =>
    //       currData.id_item === item.id_item && !currData.id_item_request
    //   );
    //   // console.log(find);

    //   if (find < 0) {
    //     const newData = Object.assign({}, item, {
    //       index_no: count,
    //       item_request_no: null,
    //       id_item_request: null,
    //       id_item_request_detail: null,
    //       id_unit: item.purchase_unit.id_unit,
    //       unit_code: item.purchase_unit.unit_code,
    //       qty: 1,
    //       base_qty: 1
    //     });

    //     // console.log(newData);

    //     this.setState({
    //       dataSource: [...dataSource, newData],
    //       count: count + 1,
    //       totalQty: totalQty + 1
    //     });

    //     clearItem([...dataSource, newData]);
    //     // console.log(this.props.item);
    //   } else if (find >= 0) {
    //     let newTotQty = 0;

    //     const updateData = dataSource.filter(
    //       currData => currData.index_no !== count
    //     );

    //     let newData = Object.assign({}, updateData[find]);
    //     newData.qty = parseFloat(newData.qty) + 1;
    //     updateData.splice(find, 1, {
    //       ...updateData[find],
    //       ...newData
    //     });
    //     updateData.map(currData => (newTotQty += parseFloat(currData.qty)));

    //     this.setState({
    //       dataSource: [...updateData],
    //       totalQty: newTotQty
    //     });

    //     clearItem([...updateData]);
    //     // console.log(this.props.item);
    //   }
    // } else

    if (!loadingTransactions && !loadingItems && items.length !== 0) {
      let newTotQty = 0;
      let newIndex = 1;
      let newData = [];
      let importedTransaction = 0;

      items.map(item => {
        // counting imported transaction
        // untuk menentukan transaction_date
        importedTransaction++;

        let init = {
          index_no: newIndex,
          id_item_request: item.id_item_request,
          item_request_no: item.item_request_no,
          id_item_request_detail: item.id_item_request_detail,
          id_item: item.id_item,
          item_code: item.item_code,
          item_name: item.item_name,
          id_unit: item.id_unit,
          unit_code: item.unit ? item.unit.unit_code : item.unit_code,
          unit_conversion: item.item
            ? item.item.unit_conversion
            : item.unit_conversion,
          unit_conv: item.item 
            ? getUnitConversion(item.id_unit, item.item.unit_conversion)
            : getUnitConversion(item.id_unit, item.unit_conversion),
          qty: null,
          base_qty: 1,
          id_base_unit: item.item
            ? item.item.base_unit.id_unit
            : item.id_base_unit,
          base_unit_code: item.item
            ? item.item.base_unit.unit_code
            : item.base_unit_code
        };

        init.qty = item.qty
            ? parseFloat(item.qty)
            : parseFloat(item.request_qty) -
              parseFloat(item.ordered_qty) / init.unit_conv -
              parseFloat(item.sent_qty) / init.unit_conv;
        init.base_qty = init.qty * init.unit_conv;

        if (init.qty > 0) {
          newData.push(init);
          newTotQty += parseFloat(init.qty);
          newIndex = parseInt(init.index_no) + 1;
        }
      });

      this.setState({
        dataSource: [...newData],
        totalQty: newTotQty,
        count: newIndex,
        importedTransaction
      });

      clearItems(newData, importedTransaction);
    } else if (clear) {
      this.setState({ dataSource: [], count: 1, importedTransaction: 0 });
      handleSaveItemList([], 0);
    }
  }

  handleDelete = index_no => {
    const { handleSaveItemList } = this.props;
    let newTotQty = 0;
    let importedTransaction = 0;

    const dataSource = this.state.dataSource;

    let newData = dataSource.filter(currData => currData.index_no !== index_no);
    newData.map((currData, index) => {
      currData.index_no = index + 1;
      newTotQty += parseFloat(currData.qty);

      // counting imported transaction
      // untuk menentukan transaction_date
      importedTransaction++;
    });

    this.setState({
      dataSource: [...newData],
      count: newData.length + 1,
      totalQty: newTotQty,
      importedTransaction
    });
    handleSaveItemList(newData, importedTransaction);
  };

  handleSave = row => {
    const { dataSource, importedTransaction } = this.state;
    const { handleSaveItemList } = this.props;
    const newData = dataSource;

    const index = newData.findIndex(
      currData => row.index_no === currData.index_no
    );

    row.unit_conv = getUnitConversion(row.id_unit, row.unit_conversion);
    row.base_qty = row.qty * row.unit_conv;

    const newTotQty =
      this.state.totalQty +
      parseFloat(row.qty) -
      parseFloat(newData[index].qty);

    newData.splice(index, 1, {
      ...newData[index],
      ...row
    });

    this.setState({ dataSource: [...newData], totalQty: newTotQty });
    handleSaveItemList(newData, importedTransaction);
  };

  render() {
    // console.log(this.state.dataSource);

    const { dataSource } = this.state;
    const components = {
      body: {
        row: EditableFormRow,
        cell: EditableCell
      }
    };

    const columns = [
      {
        title: getLanguage({ id: "no." }),
        dataIndex: "index_no",
        width: "5%",
        align: "right",
        render: text => {
          if (isNaN(text)) {
            return {
              children: text,
              props: {
                colSpan: 4,
                align: "right"
              }
            };
          } else {
            return {
              children: text,
              props: {
                colSpan: 1
              }
            };
          }
        }
      },
      {
        title: (
          <Tooltip placement="top" title={<IntlMessages id="itemRequestNo" />}>
            <a href="javascript:;"><IntlMessages id="rqNo" /> </a>
            {/* <br />
            <Icon type="question-circle" /> */}
          </Tooltip>
        ),
        dataIndex: "item_request_no",
        width: "10%",
        render: (text, row) => {
          if (isNaN(row.index_no)) {
            return {
              props: {
                colSpan: 0
              }
            };
          }

          return text && text.length > 10 ? (
            <Tooltip placement="bottom" title={text}>
              {text.substring(0, 10) + "..."}
            </Tooltip>
          ) : text
        }
      },
      {
        title: getLanguage({ id: "code" }),
        dataIndex: "item_code",
        width: "20%",
        render: (text, row) => {
          if (isNaN(row.index_no)) {
            return {
              props: {
                colSpan: 0
              }
            };
          } else if (text && text.length > 25) {
            return (
              <Tooltip placement="bottom" title={text}>
                {text.substring(0, 25) + "..."}
              </Tooltip>
            );
          } else {
            return text;
          }
        }
      },
      {
        title: getLanguage({ id: "itemName" }),
        dataIndex: "item_name",
        width: "40%",
        render: (text, row) => {
          if (isNaN(row.index_no)) {
            return {
              props: {
                colSpan: 0
              }
            };
          } else if (text && text.length > 40) {
            return (
              <Tooltip placement="bottom" title={text}>
                {text.substring(0, 40) + "..."}
              </Tooltip>
            );
          } else {
            return text;
          }
        }
      },
      {
        title: getLanguage({ id: "quantity" }),
        dataIndex: "qty",
        width: "15%",
        // editable: true,
        align: "center",
        onCell: row => {
          if (!isNaN(row.index_no)) {
            if (!this.props.status || this.props.status === 1) {
              return {
                record: row,
                dataIndex: "qty",
                editable: true,
                handleSave: this.handleSave,
                colSpan: 1
              };
            } else {
              return {
                record: row,
                dataIndex: "qty",
                colSpan: 1
              };
            }
          } else {
            return {
              colSpan: 1
            };
          }
        }
      },
      {
        title: getLanguage({ id: "unit" }),
        width: "15%",
        dataIndex: "unit_code",
        onCell: row => {
          if (!isNaN(row.index_no)) {
            if (!this.props.status || this.props.status === 1) {
              return {
                record: row,
                dataIndex: "unit_code",
                editable: true,
                handleSave: this.handleSave,
                colSpan: 1
              };
            } else {
              return {
                record: row,
                dataIndex: "unit_code",
                colSpan: 1
              };
            }
          } else {
            return {
              colSpan: 3
            };
          }
        }
      },
      {
        title: (
          <Tooltip placement="top" title={<IntlMessages id="baseQuantity" />}>
            <a href="javascript:;"><IntlMessages id="baseQty" /> </a>
          </Tooltip>
        ),
        width: "10%",
        dataIndex: "base_qty",
        render: (text, row) => {
          if (isNaN(row.index_no)) {
            return {
              props: {
                colSpan: 0
              }
            };
          }

          return <><NumberFormat value={text} /> {row.base_unit_code} </>;
        }
      },
      
    ];

    let operation = 
      {
        title: "",
        dataIndex: "operation",
        width: "5%",
        align: "center",
        fixed: "right",
        render: (text, row) => {
          if (isNaN(row.index_no)) {
            return {
              props: {
                colSpan: 1
              }
            };
          } 
          else {
            return (
              <Popconfirm
                title={<IntlMessages id="confirmDelete" />}
                onConfirm={() => this.handleDelete(row.index_no)}
                okText={<IntlMessages id="yes" />}
                cancelText={<IntlMessages id="no" />}
              >
                <Tooltip
                  title={<IntlMessages id="delete" />}
                >
                  <Button
                    //style={{ borderRadius: "50%" }}
                    className="invoiceDltBtn"
                    size="small"
                  >
                    <i className="ion-trash-b" />
                  </Button>
                </Tooltip>              
              </Popconfirm>
            );
          }
        }
      }

    if ((!this.props.status || this.props.status === 1 ) && columns.length === 7){
      columns.push(operation)
    }

    const total = getTotal(dataSource);
    const info = [
      {
        index_no: getLanguage({ id: "totalItem" }),
        qty: <NumberFormat value={parseFloat(this.state.count) - 1} />
      },
      {
        index_no: getLanguage({ id: "totalQuantity" }),
        qty: <NumberFormat value={total} />
      }
    ];
    const modifiedData = dataSource.length > 0 ? [...dataSource, ...info] : [];

    return (
      <CardWrapper>
        <div>
          <Scrollbars style={{ width: "100%" }}>
            <TableWrapper
              className="invoiceListTable"
              components={components}
              rowKey={record => record.index_no}
              rowClassName={() => "editable-row"}
              dataSource={modifiedData}
              columns={columns}
              pagination={false}
            />
          </Scrollbars>
        </div>
      </CardWrapper>
    );
  }
}

export default EditableTable;
